@import 'normalize';

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body > h1 {
  font-size: 40px;
  margin: 1em 2.5rem;
}

.movie-info {
  display: flex;
  padding: 30px 2.5rem;
  flex-direction: column;
  justify-content: center;
  border-top: 1px solid #eeeeee;
  label {
    font-size: 24px;
    color: #1b1b1c;
  }
  > div {
    display: grid;
    grid-template-columns: 1fr 3fr;
    align-items: center;
    margin: 1rem 0;
  }
  input,
  select {
    font-family: Helvetica;
    font-size: 24px;
    text-align: left;
    padding: 0 25px;
    border: 2px solid #cccccc;
    border-radius: 8px;
  }
  input:focus,
  select:focus {
    outline: #61a0ff auto 1px;
  }
  input::placeholder,
  select.select--none {
    color: #aaaaaa;
  }
  input,
  select,
  .info__rating {
    height: 72px;
  }
  .rating__stars {
    place-self: center right;
    display: flex;
    > * {
      margin: 0 4px;
    }
    img {
      transition: transform 0.2s ease-out;
    }
    img:hover {
      cursor: pointer;
      transform: scale(1.25);
    }
  }
  select {
    -webkit-appearance: none;
    -moz-appearance: none;
    background: transparent;
    background-image: url('../assets//arrow.svg');
    background-repeat: no-repeat;
    background-position-x: 95%;
    background-position-y: 50%;
  }
}

.info__button {
  font-size: 24px;
  color: white;
  background-image: linear-gradient(180deg, #94bfff 0%, #61a0ff 100%);
  border: 1px solid #61a0ff;
  box-shadow: 0 2px 4px 0 rgba(97, 160, 255, 0.5),
    0 4px 8px 0 rgba(97, 160, 255, 0.25);
  border-radius: 8px;
  height: 72px;
  transition: transform .2s ease-out, filter .2s ease-out, box-shadow .2s ease-out;
  &:hover {
    filter: brightness(120%);
    cursor: pointer;
    transform: translateY(-3px);
    box-shadow: 0 5px 4px 0 rgba(97, 160, 255, 0.5),
    0 7px 8px 0 rgba(97, 160, 255, 0.25);
  }
}

.movies-list {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  .movies-list__item {
    display: grid;
    grid-template-columns: 64px 1fr 0.5fr;
    grid-template-areas:
      'icon title rating'
      'icon cat rating';
    border-top: 1px solid #eeeeee;
    padding: 30px 2.5rem;
    align-items: center;
    justify-self: center;
    column-gap: 25px;
  }
  .item__icon {
    --size: 64px;
    background: #61a0ff;
    font-size: 16px;
    text-align: center;
    display: flex;
    justify-content: center;
    color: #fff;
    align-items: center;
    width: var(--size);
    height: var(--size);
    border-radius: 50%;
    grid-area: icon;
  }
  .item__title {
    grid-area: title;
    font-weight: bold;
    font-size: 24px;
    color: #000000;
  }
  .item__cat {
    grid-area: cat;
    font-size: 16px;
    color: #aaaaaa;
  }
  .item__rating {
    grid-area: rating;
    display: flex;
    > * {
      margin: 0 4px;
    }
  }
  .movies-list__item:last-child {
    border-bottom: 1px solid #eeeeee;
  }
}
